body {
  text-align: center;
}

a {
  color: #000;
  text-decoration: none;
  display: flex;
}

button {
  width: 100px;
  height: 40px;
  color: #000;
  background-color: #f3ba00;
  border: 1px solid #cbcb85;
  border-radius: 20px;
  font-weight: 600;
}

button:disabled, button[disabled] {
  color: #666;
  background-color: #ccc;
  border: 1px solid #999;
}

